@import url(https://fonts.googleapis.com/css?family=Noto+Sans+KR|Roboto);
#app {
  font-family: 'Roboto', 'Noto Sans KR', sans-serif;
}
* {
  margin:0;
  padding:0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
body {
  height:100%;
  background: #f4f4f4;
}
.btn {
  border:none;
  background: none;
  cursor: pointer;
  outline: none;
}








































.header {
    width:100%;
    background: white;
    margin:0;
    padding:0;
}
.header-area {
    width:60%;
    height:80px;
    margin:0 auto;
    overflow: auto;
    line-height: 80px;
}
.header-area .logo {
    float:left;
}
.header-area .logo a {
    color:#00A591;
    text-decoration: none;
}
.header-area .menu-area {
    height:100%;
    float:right;
}
.header-area .menu-area ul {
    height:100%;
}
.header-area .menu-area li {
    height:100%;
    line-height: 80px;
    float:left;
    list-style: none;
    padding:0 20px;
}

.banner-area {
  background: #eeeeee;
  position: relative;
  height:450px;
}
.banner-area::before {
  content:'';
  display:block;
  width:100%;
  position: absolute;
  top:0;
  bottom:0;
  left:0;
  right:0;
  background: url('https://ph.dailysecu.com/news/photo/201811/40971_33549_5751.jpg') no-repeat 50% 50%;
}
.banner-txt {
  position: absolute;
  top:50%;
  left:50%;
  padding:2em 5em;
  -webkit-transform:translate(-50%, -50%);
          transform:translate(-50%, -50%);
  background: rgba(0, 0, 0, 0.3);
  margin:0 auto;
  color:white;
  text-align: center;
  font-size:1.5rem;
}
.banner-txt .btn-review-write {
  border:1px solid white;
  width:100%;
  padding:1rem 0;
  margin-top:1rem;
  font-size:0.8rem;
  color:white;
}
.banner-txt .btn-review-write:hover {
  background: #00A591;
}


.main-contents {
  width:60%;
  margin:2rem auto;
}
.main-contents .card {
  margin-top:2rem;
  background: white;
  width:32%;
}
.main-contents .card img {
  width:100%;
}
.hall-img {
  height:200px;
  overflow: hidden;
  background: #333333;
}
.hall-text {
  padding:1rem;
}
.hall-text .address {
  margin:1rem 0;
}
.card-area {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}
.card {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.3);
          box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.3);
}
.icon-area {
  overflow: auto;
  padding:1rem;
  border-top:1px solid #cccccc;
  margin-top:auto;
}
.icon-area .stars {
  float:left;
}
.icon-area .like {
  float:right;
}
.icon-area .like .btn {
  color:#cccccc;
}
.content-top {
  overflow: auto;
  position: relative;
}
.contents-title {
  float: left;
}
.order-btn {
  float: right;
  height:100%;
  position: absolute;
  right:0;
  bottom:0;
}
.order-btn .btn {
  font-size:1rem;
  margin-left:1rem;
  height:100%;
}
.loding {
  clear: both;
  text-align: center;
  margin:2rem 0;
  color:#cccccc;
}
.icon-area .like .likeOn {
  color:red;
}
.star {
  color: #00A591;
  font-weight:bold;
}
.review {
  color: #00A591;
  font-weight:bold;
}

.review-contents {
  width:60%;
  margin:0 auto;
}
.box {
  background: white;
  margin:2rem 0;
}
.ratings {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.ratings h3 {
  font-size:0.8rem;
}
.ratings p {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.ratings .rating {
  -webkit-box-flex:1;
      -ms-flex:1;
          flex:1;
  text-align: center;
  padding:2rem;
}
.hall-info {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.info-txt {
  padding:2rem;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.info-img {
  -webkit-box-flex: 2;
      -ms-flex: 2;
          flex: 2;
}
.info-img img {
  width:100%;
}
.reviews {
  padding:2rem;
}
.review-txt {
  border-top:1px solid #cccccc;
  margin-top:1rem;
  padding-top:1rem;
}
.rating-num {
  margin:0.8rem;
  font-size:1.5rem;
}

.contents-write {
  background: white;
  width:60%;
  margin:0 auto;
  padding:2rem;
}

.fa-icon {
  display: inline-block;
  fill: currentColor;
}
.fa-flip-horizontal {
  -webkit-transform: scale(-1, 1);
          transform: scale(-1, 1);
}
.fa-flip-vertical {
  -webkit-transform: scale(1, -1);
          transform: scale(1, -1);
}
.fa-spin {
  -webkit-animation: fa-spin 1s 0s infinite linear;
          animation: fa-spin 1s 0s infinite linear;
}
.fa-inverse {
  color: #fff;
}
.fa-pulse {
  -webkit-animation: fa-spin 1s infinite steps(8);
          animation: fa-spin 1s infinite steps(8);
}
@-webkit-keyframes fa-spin {
0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
@keyframes fa-spin {
0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
